.nav-bar {
    width: 100%;
    background: linear-gradient(to right, rgb(15,33,110), rgb(59,100,144));
    font-size: 16px;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    font-size: 16px;
}

.nav-bar a {
    text-decoration: none;
    
}

.nav-bar a:link,
.nav-bar a:visited {
    color: white
}

.nav-bar a:hover,
.nav-bar a:active {
    color: rgb(211, 211, 211);
}


.nav-list {
    max-width: 1200px;
    margin:0 auto;
    display: flex;
    list-style: none;   
    height: 50px;
    align-items: center;
    justify-content: left;
   color: white;
}

.logo img {
    height: 50px;
    flex-grow: 1;
}

.nav-list-sec {
    display: flex;
    list-style: none;
    justify-content: space-around;
    flex-grow: 2;    
    height: 100%;
    align-items: center;
}

.nav-list-sec li {
    flex-grow: 1;
    text-align: center;
    height: 100%;  
    padding-top: 15px; 
}

.form {
    flex-grow: 1;
    margin-left: auto;
    align-content: center;
    display: inline-block;
    background-color: white;
    border-radius: 25px;
}

.input {
    width: 85%;
    border:none;
    border-radius: 25px;
    height: 30px;
    outline: none !important;
    padding-left: 10px;
}

.form a {
    padding-left: 10px;
    margin-top: 10px;
    
}

ion-icon[name="search-outline"] {
    color: black;
    font-size: 130%;
    margin-bottom: -5px;    
}

.account {
    flex-grow: 1;
    text-align: center;
    font-size: 140%;
}


